import React, { Component } from "react";
import Fade from "react-reveal";
import Zmage from "react-zmage";
import "../styles/about.css";

class About extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currentImageIndex: 0,
    };
  }

  componentDidMount() {
    this.startCarousel();
  }

  componentWillUnmount() {
    clearInterval(this.carouselInterval);
  }

  startCarousel() {
    this.carouselInterval = setInterval(() => {
      this.setState((prevState) => ({
        currentImageIndex: (prevState.currentImageIndex + 1) % 3,
      }));
    }, 5000);
  }

  render() {
    if (!this.props.data) return null;

    const name = this.props.data.name;
    const profilepic = "images/" + this.props.data.image;
    const bio = this.props.data.bio;
    const street = this.props.data.address.street;
    const city = this.props.data.address.city;
    const state = this.props.data.address.state;
    const zip = this.props.data.address.zip;
    const phone = this.props.data.phone;
    const email = this.props.data.email;
    const resumeDownload = this.props.data.resumedownload;

    const carouselImages = [
      { src: profilepic, alt: "Professional Headshot" },
      { src: "images/about/work-environment.jpg", alt: "Work Environment" },
      { src: "images/about/team-collaboration.jpg", alt: "Team Collaboration" },
    ];

    return (
      <section id="about">
        <Fade duration={1000}>
          <div className="row">
            <div className="three columns">
              <div className="profile-carousel">
                <Zmage
                  src={carouselImages[this.state.currentImageIndex].src}
                  alt={carouselImages[this.state.currentImageIndex].alt}
                  className="profile-pic"
                />
                <div className="carousel-dots">
                  {carouselImages.map((_, index) => (
                    <span
                      key={index}
                      className={`dot ${
                        index === this.state.currentImageIndex ? "active" : ""
                      }`}
                      onClick={() =>
                        this.setState({ currentImageIndex: index })
                      }
                    />
                  ))}
                </div>
              </div>
            </div>
            <div className="nine columns main-col">
              <h2>About Me</h2>
              <div className="bio-container">
                <p>{bio}</p>
              </div>
              <div className="row">
                <div className="columns contact-details">
                  <h2>Contact Details</h2>
                  <p className="address">
                    <span>{name}</span>
                    <br />
                    <span>
                      {city}, {state}
                    </span>
                    <br />
                    <span>{email}</span>
                  </p>
                </div>
                <div className="columns download">
                  <p>
                    <a href={resumeDownload} className="button">
                      <i className="fa fa-download"></i>Download Resume
                    </a>
                  </p>
                </div>
              </div>
              <div className="skills-badges">
                <h3>Core Skills</h3>
                <div className="badge-container">
                  <div className="badge">
                    <i className="fa fa-code"></i>
                    <span>Web Development</span>
                  </div>
                  <div className="badge">
                    <i className="fa fa-mobile"></i>
                    <span>Responsive Design</span>
                  </div>
                  <div className="badge">
                    <i className="fa fa-database"></i>
                    <span>Database Management</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </Fade>
      </section>
    );
  }
}

export default About;
